.chat-box {
  width: 100%;
  height: 90vh;
  background-color: bisque;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow-y: auto;
  overflow: hidden;
}
.chat-header {
  height: 6%;
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  background-color: rgb(241, 163, 123);
}

.chat-header-title {
  margin-left: 20px;
  font-weight: bold;
  font-size: large;
  display: flex;
  align-items: center;
  position: absolute;
  left: 10px;
}

.chat-header-icon {
  position: absolute;
  right: 10px;
}

.chart-box-form-input {
  /* background-color: rgb(34, 37, 41); */
  width: 60%;
  border-radius: 8px;
  outline: none;
  border: 1px solid rgb(20, 39, 20);
}

.chat-box-form {
  width: 100%;
  height: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  /* bottom: 1px; */
  background-color: rgb(71, 21, 21);
  margin-bottom: 5px;
}
.chat-box-message-container{
  height: 85%;
  padding: 20px;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  display: flex;
  flex-direction: column-reverse;
  align-items: flex-start;
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-color: black black;
  scrollbar-width: thin;
}
.chart-box-form-input{
 height: 80%;
}

.chat-box-time-info{
  width: 100%;
  text-align: center;
  margin-bottom: 20px;
}

.message-rendered-box{
  display: flex;
  width: 100%;
  /* align-items:flex-start; */
  margin-left: 2px;
  margin-bottom: 20px;
}
.chatbox-userprofile-container{
  margin-left: 5px;
  margin-right: 5px;
  display: flex;
  align-items: center;
}